import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { CoreModule } from './core/core.module';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { HttpClientModule } from '@angular/common/http';
import { RECAPTCHA_SETTINGS, RecaptchaSettings, RecaptchaFormsModule, RecaptchaModule } from 'ng-recaptcha';
import jsonString from '@stringResources/app-strings.json';
import { SesionProvider } from './tramites/mincultura/providers/sesion';
import { SolicitudProvider } from './tramites/mincultura/providers/solicitud';
import { PdfViewerModule } from 'ng2-pdf-viewer';
import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';

const COMPONENTS_PROVIDER=[
  SesionProvider,
  SolicitudProvider
]


@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    CoreModule,
    HttpClientModule,
    NgbModule,
    BrowserAnimationsModule,
    PdfViewerModule
  ],
  providers: [...COMPONENTS_PROVIDER,
    {
      provide: RECAPTCHA_SETTINGS,
      useValue: { siteKey: jsonString["client-side-recaptcha-key"] } as RecaptchaSettings,
    },
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }

platformBrowserDynamic().bootstrapModule(AppModule);
